import { getAllGroup } from '@/api/deviceGroup';
import { getDevice } from '@/api/devicelist';
import { setInterval } from 'timers';
export default {
  name: 'Home',
  data: function data() {
    return {
      isNotGroup: false,
      map: null,
      markerArray: [],
      tableData: [],
      totalCount: 30,
      pageIndex: 1,
      keyword: '',
      dialogVisible: true,
      mapProgress: 0
    };
  },
  created: function created() {
    var _this = this;

    setInterval(function () {
      _this.dialogVisible = false;
    }, 3000);
  },
  mounted: function mounted() {
    if (this.$store.state.user.config.hasGroup) {
      this.getGroup();
    } else {
      this.getData(1);
    }

    this.mapInit();
  },
  beforeDestroy: function beforeDestroy() {
    this.map.destroy();
  },
  methods: {
    getGroup: function getGroup() {
      var _this2 = this;

      getAllGroup().then(function (res) {
        var data = res.data;
        var arr = [];
        var lastArr = [];

        _this2.$set(_this2, 'tableData', data);

        queryList(data, arr);

        function queryList(data, arr) {
          // 
          for (var i = 0; i < data.length; i++) {
            lastArr.push(data[i]);
            var children = data[i].children;

            if (children.length == 0) {
              arr.push(data[i]);
            } else {
              queryList(children, arr);
            }
          }

          return arr;
        }

        lastArr.forEach(function (item) {
          _this2.markerArray.push(_this2.setMarker(item));
        });

        _this2.map.plugin(['AMap.MarkerClusterer'], function () {
          // eslint-disable-next-line no-undef
          var cluster = new AMap.MarkerClusterer(_this2.map, _this2.markerArray, {
            gridSize: 80
          });
          cluster.setMinClusterSize(5);
          _this2.mapProgress = 100;
        });
      });
    },
    getData: function getData(n) {
      var _this3 = this;

      this.isNotGroup = true;
      return new Promise(function (resolve) {
        getDevice({
          isTop: true,
          pageIndex: n,
          pageSize: 200
        }).then(function (res) {
          // 200 table
          if (n === 1) {
            _this3.$set(_this3, 'tableData', res.data.items);

            _this3.totalCount = res.data.totalCount;
          }

          res.data.items.forEach(function (item) {
            _this3.markerArray.push(_this3.setMarker(item));
          });
          var step = n * res.data.pageSize / res.data.totalCount;

          if (step >= 1) {
            _this3.mapProgress = 100;
            resolve();
          } else {
            _this3.mapProgress = Math.floor(step * 100);

            _this3.getData(n + 1);
          }
        });
      }).then(function () {
        _this3.mapProgress = 100;

        _this3.map.plugin(['AMap.MarkerClusterer'], function () {
          // eslint-disable-next-line no-undef
          var cluster = new AMap.MarkerClusterer(_this3.map, _this3.markerArray, {
            gridSize: 80
          });
          cluster.setMinClusterSize(5);
        });
      });
    },
    mapInit: function mapInit() {
      // eslint-disable-next-line no-undef
      this.map = new AMap.Map(this.$refs.map, {
        center: [104, 36],
        resizeEnable: true,
        zoom: 5
