          vm.$router.push({
            path: vm.$store.state.user.config.isPro ? "/ExpertSystem/EquipmentManagement/list/".concat(this.getExtData().id) : "/EquipmentManagement/list/".concat(this.getExtData().id)
          });
        });
      } else {
        marker.on('click', function () {
          vm.$router.push({
            path: vm.$store.state.user.config.isPro ? "/ExpertSystem/EquipmentManagement/status/".concat(this.getExtData().id) : "/EquipmentManagement/status/".concat(this.getExtData().id)
          });
        });
      }

      return marker;
    },
    handleTableSelected: function handleTableSelected(val) {
      this.map.setCenter([val.lng, val.lat]);
      this.map.setZoom(20);
    },
    init: function init() {
      var _this4 = this;

      getDevice({
        isTop: true,
        keyword: this.keyword,
        pageIndex: this.pageIndex,
        pageSize: 200
      }).then(function (res) {
        _this4.$set(_this4, 'tableData', res.data.items);

        _this4.pageIndex = res.data.pageIndex;
        _this4.pageSize = res.data.pageSize;
        _this4.totalCount = res.data.totalCount;
      });
    },
    search: function search() {
      this.pageIndex = 1;
      this.init();
    },
    handleCurrentChange: function handleCurrentChange(val) {
      this.pageIndex = val;
      this.init();
    }
  }
};